import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { SubMenu as RcSubMenu } from 'rc-menu';
class SubMenu extends React.Component {
    // private subMenu: any;
    // onKeyDown = (e: React.MouseEvent<HTMLElement>) => {
    //   this.subMenu.onKeyDown(e);
    // };
    // saveSubMenu = (subMenu: any) => {
    //   this.subMenu = subMenu;
    // };
    render() {
        const { popupClassName, rootPrefixCls } = this.props;
        // console.log('SubMenu',this.props);
        const { rootTheme, rootSize, rootMode } = this.context;
        // 横向菜单弹出的样式为light
        const theme = rootMode === 'horizontal' ? 'light' : rootTheme;
        return (<RcSubMenu {...this.props} popupClassName={classNames({
            [`${rootPrefixCls}-${theme}`]: !!theme,
            [`${rootPrefixCls}-${rootSize}`]: rootSize && rootSize !== 'default',
        }, popupClassName)}/>);
    }
}
// fix issue:https://github.com/ant-design/ant-design/issues/8666
SubMenu.isSubMenu = 1;
SubMenu.contextTypes = {
    rootTheme: PropTypes.string,
    rootSize: PropTypes.string,
    rootMode: PropTypes.string,
};
export default SubMenu;
